import React from "react";
import "./Feedback.css";
import F2 from "../../media/f2.f69462dd.jpg";
import F3 from "../../media/f3.af7211d8.jpg";
import F4 from "../../media/f4.61c4a1d0.jpg";
import F5 from "../../media/f5.3330c9f2.jpg";
import F1 from "../../media/f1.bb943822.jpg";
import F7 from "../../media/f7.90039530.jpg";
import F8 from "../../media/f8.f962634b.jpg";
import F9 from "../../media/f9.e9dc7831.jpg";
import F6 from "../../media/f6.24782928.jpg";
import MainHeading from "../../components/MainHeading/MainHeading";

const Feedback = () => {
  return (
    <div className="student-feedback-container">
      <MainHeading title="STUDENT feedback" color="2d2e55" />
      <div className="row">
        <div className="column">
          <img src={F2} alt="feedback" />
          <img src={F3} alt="feedback" />
        </div>
        <div className="column">
          <img src={F4} alt="feedback" />
          <img src={F5} alt="feedback" />
          <img src={F1} alt="feedback" />
        </div>
        <div className="column">
          <img src={F7} alt="feedback" />
          <img src={F8} alt="feedback" />
        </div>
        <div className="column">
          <img src={F9} alt="feedback" />
          <img src={F6} alt="feedback" />
        </div>
      </div>
    </div>
  );
};

export default Feedback;
