import React from "react";
import "./RefundPolicy.css";
import Nav from "../../components/Navigation/Nav";
import MainHeading from "../../components/MainHeading/MainHeading";
import Footer from "../../Sections/Footer/Footer";

const RefundPolicy = () => {
  return (
    <>
      <Nav />
      <div className="privacy-container refund-container">
        <div className="main-heading--container">
          <MainHeading title="REFUND POLICY" color="2d2e55" />
        </div>
        <h2 className="privacy-policy-heading">IMPORTANT</h2>
        <p className="privacy-policy-para" style={{ textAlign: "center" }}>
          Fees Once paid is not Refundable or Transferable in any circumstances
        </p>
      </div>
      <Footer />
    </>
  );
};

export default RefundPolicy;
