import React, { useContext } from "react";
import "./CourseSection.css";
import Course from "../../components/Course/Course";
import MainHeading from "../../components/MainHeading/MainHeading";
import { popularCoursesData } from "../../Data/PopularCourses";
import { UserDataContext } from "../../context/ContextData";

const CourseSection = () => {
  const { courseSection } = useContext(UserDataContext);
  return (
    <div ref={courseSection} className="course-section-container" id="courses">
      <MainHeading title="courses" color="f5f4f5" />
      <div className="course-container">
        {popularCoursesData.map((ele, ind) => {
          return (
            <Course
              key={ind}
              image={ele.image}
              selectedCourse={ele.selectedCourse}
              amount={ele.price}
            />
          );
        })}
      </div>
    </div>
  );
};

export default CourseSection;
