import React, { useState, useContext } from "react";
import "./Nav.css";
import HomeIcon from "@mui/icons-material/Home";
import { Link } from "react-router-dom";
import InfoIcon from "@mui/icons-material/Info";
import NoteAltIcon from "@mui/icons-material/NoteAlt";
import PermContactCalendarIcon from "@mui/icons-material/PermContactCalendar";
import MenuIcon from "@mui/icons-material/Menu";
import CloseIcon from "@mui/icons-material/Close";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import { UserDataContext } from "../../context/ContextData";
import Logo from "../../media/mkbang-logo.png";
import SpeedIcon from "@mui/icons-material/Speed";

const Nav = ({ anim }) => {
  const [nav, setNav] = useState(false);

  const handleContact = () => {
    window.scrollTo({ top: 12000, left: 0 });
    setNav(false);
  };

  const { gotoCourse, gotoFaculty, gotoResult, gotoAbout } =
    useContext(UserDataContext);

  const handleCourse = () => {
    setNav(false);
    gotoCourse();
  };

  const handleFaculty = () => {
    setNav(false);
    gotoFaculty();
  };
  const handleResult = () => {
    setNav(false);
    gotoResult();
  };

  const handleAbout = () => {
    setNav(false);
    gotoAbout();
  };

  return (
    <div
      className="navigation"
      style={{ animationDelay: `${anim ? "4.5s" : "0s"}` }}
    >
      <div className="burger-navigation">
        <div className="code-eater-nav-logo">
          <img src={Logo} alt="spoon" />
        </div>
        {nav ? (
          <CloseIcon onClick={() => setNav(!nav)} fontSize="large" />
        ) : (
          <MenuIcon onClick={() => setNav(!nav)} fontSize="large" />
        )}
      </div>
      <ul style={{ transform: `${nav ? "translateY(-5px)" : ""}` }}>
        <Link to="/" style={{ textDecoration: "none" }}>
          <li>
            <HomeIcon
              fontSize={`${nav ? "large" : "small"}`}
              style={{ margin: "0 5px" }}
            />{" "}
            home
          </li>
        </Link>
        <li onClick={() => handleCourse()}>
          <NoteAltIcon
            fontSize={`${nav ? "large" : "small"}`}
            style={{ margin: "0 5px" }}
          />{" "}
          courses
        </li>
        <li onClick={() => handleFaculty()}>
          <PeopleAltIcon
            fontSize={`${nav ? "large" : "small"}`}
            style={{ margin: "0 5px" }}
          />{" "}
          FACULTIES
        </li>
        <li onClick={() => handleResult()}>
          <SpeedIcon
            fontSize={`${nav ? "large" : "small"}`}
            style={{ margin: "0 5px" }}
          />{" "}
          RESULT
        </li>
        <li onClick={() => handleAbout()}>
          <InfoIcon
            fontSize={`${nav ? "large" : "small"}`}
            style={{ margin: "0 5px" }}
          />{" "}
          about
        </li>
        <li onClick={() => handleContact()}>
          <PermContactCalendarIcon
            fontSize={`${nav ? "large" : "small"}`}
            style={{ margin: "0 5px" }}
          />{" "}
          connect
        </li>
      </ul>
    </div>
  );
};

export default Nav;
