import React, { useState, useContext } from "react";
import { UserDataContext } from "../../context/ContextData";
import "./Course.css";

const Course = ({ image, selectedCourse, amount }) => {
  const [modal, setModal] = useState(false);
  const [step, setStep] = useState(1);

  const { handleSend, dispalyRazorpay, setPayData } =
    useContext(UserDataContext);

  const handleModal = () => {
    setModal(true);
    setPayData({
      course: selectedCourse,
      amount: amount,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setStep(2);
    handleSend({
      selectedCourse,
      name: e.target[0].value,
      email: e.target[1].value,
      dob: e.target[2].value,
      fatherName: e.target[3].value,
      motherName: e.target[4].value,
      address: e.target[5].value,
      tenthPercentage: e.target[6].value,
      gender: e.target[7].value,
      mobile: e.target[8].value,
      previousSchool: e.target[9].value,
    });
    dispalyRazorpay();
  };
  return (
    <div className="main-course--container">
      <img className="card-image" src={image} alt="course" />
      <div className="course-card--content">
        <div className="course-button-container">
          <a onClick={() => handleModal()} rel="noreferrer" target="_blank">
            enroll now
          </a>
        </div>
      </div>
      {modal ? (
        <div id="myModal" className="modal">
          {step === 1 ? (
            <div className="modal-content">
              <span className="close" onClick={() => setModal(false)}>
                &times;
              </span>
              <div className="form-container-step2">
                <div className="form-step2-div1">
                  <h2>PLEASE FILL OUT THE FIELDS</h2>
                  <h3>SELECTED COURSE : {selectedCourse}</h3>
                </div>
                <form className="form-container--modal" onSubmit={handleSubmit}>
                  <input
                    className="form-input-modal"
                    placeholder="Full Name"
                    type="text"
                    required
                  />
                  <input
                    className="form-input-modal"
                    placeholder="Email"
                    type="email"
                    required
                  />
                  <input
                    type="date"
                    className="form-input-modal"
                    placeholder="DOB"
                    required
                  />
                  <input
                    className="form-input-modal"
                    placeholder="Father's Name"
                    type="text"
                    required
                  />
                  <input
                    className="form-input-modal"
                    placeholder="Mother's Name"
                    type="text"
                    required
                  />
                  <input
                    className="form-input-modal"
                    placeholder="Residential Address"
                    type="text"
                    required
                  />
                  <input
                    className="form-input-modal"
                    placeholder="10th percentage"
                    type="number"
                    required
                  />

                  <select
                    className="form-input-modal form-input--select"
                    id="gender"
                    name="gender"
                    required
                  >
                    <option value="male">Male</option>
                    <option value="female">Female</option>
                  </select>
                  <input
                    className="form-input-modal"
                    type="number"
                    placeholder="Mobile"
                  />
                  <input
                    className="form-input-modal"
                    placeholder="Previous School Name"
                    type="text"
                    required
                  />
                  <button type="submit" className="modal-form--button">
                    PAY {amount}
                  </button>
                </form>
              </div>
            </div>
          ) : (
            <div className="modal-content please-wait--container">
              <h1 className="please-wait">
                Loading... <br />
                Please wait.
              </h1>
            </div>
          )}
        </div>
      ) : null}
    </div>
  );
};

export default Course;
