import React from "react";
import "./StudentResult.css";
import Nav from "../../components/Navigation/Nav";
import Footer from "../../Sections/Footer/Footer";
import ExploreCard from "../../components/ExploreCard/ExploreCard";
import { StudentResultData } from "../../Data/exploreAll";
import MainHeading from "../../components/MainHeading/MainHeading";
import Std from "../../media/1.7f1d9423.JPG";

const StudentResult = () => {
  window.scrollTo(0, 0);
  return (
    <>
      <Nav />
      <div
        style={{ backgroundColor: "white" }}
        className="expore-main--container"
      >
        <MainHeading title="CA FOUNDATION" color="2d2e55" />
        <h1 style={{ color: "#332FD0" }}>72.41% RESULT</h1>
        <div className="explore-card-container">
          {StudentResultData.map((ele, ind) => {
            return (
              <ExploreCard
                key={ind}
                img={ele.img}
                marks={ele.marks}
                name={ele.name}
              />
            );
          })}
        </div>
        <MainHeading title="CA INTERMEDIATE" color="2d2e55" />
        <h1 style={{ color: "#332FD0" }}>100% RESULT</h1>
        <ExploreCard key="1" img={Std} marks="495" name="BHAVANA R." />
      </div>
      <Footer />
    </>
  );
};

export default StudentResult;
