import React from 'react';
import './Button.css';
import { Link } from 'react-router-dom';


const Button = ({color,link,title}) => {
  return (
      <Link to={link} style={{textDecoration:'none'}}>
      <button style={{backgroundColor:`#${color}`}} className="main-button">{title} <img src="https://media.giphy.com/media/UrzWDQ3VTiDU84R5dx/giphy.gif" alt="arrow"/> </button>
      </Link>
  );
};

export default Button;
