import React, { useContext } from "react";
import "./Explore.css";
import MainHeading from "../../components/MainHeading/MainHeading";
import ExploreCard from "../../components/ExploreCard/ExploreCard";
import { popularExploreData } from "../../Data/popularexplore";
import Button from "../../components/Button/Button";
import { UserDataContext } from "../../context/ContextData";

const Explore = () => {
  const { resultSection } = useContext(UserDataContext);
  return (
    <div ref={resultSection} className="expore-main--container">
      <MainHeading title="STUDENT RESULT" color="f5f4f5" />
      <div className="explore-card-container">
        {popularExploreData.map((ele, ind) => {
          return (
            <ExploreCard
              key={ind}
              img={ele.img}
              marks={ele.marks}
              name={ele.name}
            />
          );
        })}
      </div>
      <Button title="view all" color="ED4933" link="student-result" />
    </div>
  );
};

export default Explore;
