import React, { createContext, useState, useRef } from "react";
import axios from "axios";

export const UserDataContext = createContext();

function loadscript(src) {
  return new Promise((resolve) => {
    const script = document.createElement("script");
    script.src = src;
    script.onload = () => {
      resolve(true);
    };
    script.onerror = () => {
      resolve(false);
    };
    document.body.appendChild(script);
  });
}
const __Dev_ = document.domain.includes("localhost");

const UserDataContextProvider = (props) => {
  //ref's
  const courseSection = useRef(null);
  const facultySection = useRef(null);
  const resultSection = useRef(null);
  const aboutSection = useRef(null);

  const gotoCourse = () =>
    window.scrollTo({ top: courseSection.current.offsetTop });
  const gotoFaculty = () =>
    window.scrollTo({ top: facultySection.current.offsetTop });
  const gotoResult = () =>
    window.scrollTo({ top: resultSection.current.offsetTop });
  const gotoAbout = () =>
    window.scrollTo({ top: aboutSection.current.offsetTop });

  //nodemailer
  const [payData, setPayData] = useState({
    course: "",
    amount: "",
  });
  const handleSend = async (data) => {
    try {
      await axios.post(`${window.location.origin}/send_mail`, {
        data,
      });
    } catch (error) {
      console.log(error);
    }
  };

  //      payment fateway
  const dispalyRazorpay = async () => {
    const res = await loadscript(
      "https://checkout.razorpay.com/v1/checkout.js"
    );

    if (!res) {
      alert("make sure you have a healthy internet connection");
    }
    const data = await axios
      .post(`${window.location.origin}/razorpay`, {
        payData,
      })
      .then((res) => {
        return res;
      });
    const options = {
      key: __Dev_ ? "rzp_live_IwGrpFQD30Uru9" : "rzp_live_IwGrpFQD30Uru9",
      currency: data.data.currency,
      amount: data.data.amount.toString(),
      name: "MKBANG Classes",
      description: data.data.course,
      image: "https://cdn.logo.com/hotlink-ok/logo-social-sq.png",
      order_id: data.data.id,
      handler: function (response) {
        alert(response.razorpay_payment_id);
        alert(response.razorpay_order_id);
        alert(response.razorpay_signature);
      },
    };
    var paymentObject = new window.Razorpay(options);
    paymentObject.open();
  };
  return (
    <div>
      <UserDataContext.Provider
        value={{
          dispalyRazorpay,
          handleSend,
          setPayData,
          courseSection,
          gotoCourse,
          facultySection,
          resultSection,
          aboutSection,
          gotoFaculty,
          gotoResult,
          gotoAbout,
        }}
      >
        {props.children}
      </UserDataContext.Provider>
    </div>
  );
};

export default UserDataContextProvider;
