import React, { useContext } from "react";
import "./BlogSection.css";
import MainHeading from "../../components/MainHeading/MainHeading";
import BlogFront from "../../components/BlogFront/BlogFront";
import { popularBlogsData } from "../../Data/popularBlogs";
import { UserDataContext } from "../../context/ContextData";

const BlogSection = () => {
  const { facultySection } = useContext(UserDataContext);
  return (
    <div ref={facultySection} className="blog-section--container">
      <MainHeading title="FACULTIES" color="2d2e55" />
      <div className="blogFront-container">
        {popularBlogsData.map((ele, ind) => {
          return (
            <BlogFront
              key={ind}
              image={ele.image}
              title={ele.title}
              post={ele.position}
              descrip={ele.descrip}
            />
          );
        })}
      </div>
    </div>
  );
};

export default BlogSection;
